import {defineComponent, ref} from "vue";
import Navigation from "@/components/Navigation";
import Main from "@/components/Main";
import Footer from "@/component/Footer";
import Container from "@/components/Container";
import Card from "@/components/Card";
import styles from "./claims.module.scss";
// import {useMapState} from "@/hooks/store";
import {queryClaimsByPage} from "@/models/claimsModels";
import Exposure from "@/components/Exposure";
import Loading from "@/components/Loading";

interface ClaimType {
	id: string,
	deal_name: string,
	wait_capital: string,
	// eslint-disable-next-line
	[propName: string]: any
}

export default defineComponent({
	name: "ClaimsList",
	setup() {
		const pageCount = ref<number>(-1)
		const pageCurrent = ref<number>(1)
		const claims = ref<ClaimType[]>([])
		const stopIntersect = ref<boolean>(false)
		const monitor = ref<boolean>(false)
		// const {
		// 	user
		// } = useMapState({
		// 	states: ['user']
		// })
		const queryClaims = (page: number) => {
			stopIntersect.value = page >= pageCount.value && pageCount.value > -1
			queryClaimsByPage({page, limit: 10})
				.then(({result, page_count}) => {
					claims.value.push(...result)
					pageCount.value = page_count
					pageCurrent.value = page
					monitor.value = true
				})
		}
		// queryClaims(pageCurrent.value)
		return () => {
			const slots = {
				navigator: () => <Navigation title={"债权列表"} />,
				default: () => <Main>
					{
						claims.value.map((item: ClaimType) => <Card class={styles.claims} key={item.id}>
							<p>{item.deal_name}</p>
							<p>
								<label>借款人：</label>
								<span>{item.real_name}</span>
							</p>
							<p>
								<label>担保方：</label>
								<span>{item.agency_name}</span>
							</p>
							<p>
								<label>咨询方：</label>
								<span>{item.advisory_name}</span>
							</p>
							<p>
								<label>待还本金：</label>
								<span>{item.wait_capital}</span>
							</p>
							<p>
								<label>待还利息：</label>
								<span>{item.wait_interest}</span>
							</p>
							<p>
								<label>到期日：</label>
								<span>{item.max_repay_time}</span>
							</p>
						</Card>)
					}
					{
						<Exposure handleIntersect={queryClaims} stopIntersect={stopIntersect.value}>
							<div>
								{
									pageCount.value > pageCurrent.value ? <><Loading /> 加载中</> : '没有更多了'
								}
							</div>
						</Exposure>
					}

				</Main>,
				footer: () => <Footer />
			}
			return <Container v-slots={slots} />
		}
	}
})