import {defineComponent, PropType} from "vue";
import styles from './navigation.module.scss'
import Flex, {Item} from "@/components/Flex";
import SvgIcon from "@/plugins/SvgIcon/SvgIcon";
import {FlexAlignItems, FlexJustifyContent} from "@/components/Flex/types";
import {useRouter} from "vue-router";

export default defineComponent({
	name: 'AppNavigation',
	props: {
		title: {
			type: String as PropType<string>,
			default: ''
		},
		hideBack: {
			type: Boolean as PropType<boolean>,
			default: false
		},
		// onClick
	},
	setup(props) {
		const router = useRouter()
		return () => <Flex class={styles.navigation} justifyContent={FlexJustifyContent.center}
			alignItems={FlexAlignItems.center}>
			{
				!props.hideBack && <div class={styles.navigationBack} onClick={router.back}>
					<SvgIcon name={"enter"} />
				</div>
			}
			<Item class={styles.navigationTitle}>
				{props.title}
			</Item>
		</Flex>
	}
})