import {defineComponent} from "vue";
import Flex, {Item} from "@/components/Flex";
import {FlexAlignContent, FlexDirection} from "@/components/Flex/types";
import styles from './container.module.scss'

export default defineComponent({
	name: 'ContainerComponent',
	setup(props, {slots}) {
		return () => <Flex direction={FlexDirection.column} alignContent={FlexAlignContent["space-between"]}
			style={{height: "100vh"}}>
			{
				slots.navigator && <Item shrink={0} class={styles.containerNavigator}>
					{slots.navigator?.()}
				</Item>
			}
			<Item grow={1} class={styles.containerMain}>
				{slots.default?.()}
			</Item>
			{
				slots.footer && <Item shrink={0} class={styles.containerFooter}>{slots.footer?.()}</Item>
			}
		</Flex>
	}
})