import {defineComponent, PropType, ref, onMounted, onBeforeUnmount, effect} from "vue";
import {onClick} from "@/types";
import styles from './exposure.module.scss'
export default defineComponent({
	name: 'ExposureComponent',
	props: {
		handleIntersect: {
			type: Function as PropType<onClick<number>>,
			required: true
		},
		stopIntersect: {
			type: Boolean as PropType<boolean>,
			default: false
		}
	},
	setup(props, {slots}) {
		const $target = ref(null)
		const triggerTimes = ref<number>(0)
		// 构建观察器
		const observer: IntersectionObserver = new IntersectionObserver(([entry]) => {
			// 目标元素与根元素相交
			if (entry && entry.isIntersecting) {
				triggerTimes.value += 1
				props.handleIntersect(triggerTimes.value)
			}
		}, {threshold: .5})
		const stop = () => {
			observer.disconnect()
		}
		onMounted(() => {
			// 观察目标元素
			// @ts-ignore
			observer.observe($target.value)
		})
		effect(() => {
			if (props.stopIntersect) {
				stop()
			}
		})
		onBeforeUnmount(stop)
		return () => <div ref={$target} class={styles.loading}>
			{
				slots.default?.()
			}
		</div>
	}
})