import {defineComponent, PropType} from "vue";
import Flex, {Item} from "@/components/Flex";
import styles from './card.module.scss'
import {FlexAlignItems, FlexDirection, FlexJustifyContent} from "@/components/Flex/types";
import SvgIcon from "@/plugins/SvgIcon/SvgIcon";
import {onClick} from "@/types";

export default defineComponent({
	name: "CardComponent",
	props: {
		titleEnter: {
			type: Boolean as PropType<boolean>,
			default: false
		},
		onClick: {
			type: Function as PropType<onClick<MouseEvent>>
		},
		item: {
			type: Object as PropType<object>,
			default: () => ({})
		},
	},
	setup(props, {slots}) {
		const clickEventHandler = (e: MouseEvent) => {
			props.onClick?.(e)
		}
		return () => <Flex
			class={styles.card}
			direction={FlexDirection.column}
			onClick={clickEventHandler}
		>
			{
				slots.title && <Item class={styles.cardTitle}>
					<Flex style={{maxHeight: "100%"}} alignItems={FlexAlignItems.center}
						justifyContent={FlexJustifyContent["space-between"]}>
						<Item>
							{slots.title(props.item)}
						</Item>
						{
							props.titleEnter && <Item>
								<SvgIcon name={"enter"} style={{color: "#333"}} />
							</Item>
						}
					</Flex>
				</Item>
			}
			<Item class={styles.cardBody}>
				{slots.default?.(props.item)}
			</Item>
		</Flex>
	}
})