import {post} from "@/utils/http";

/**
 * 查询债权列表
 * @param limit
 * @param page
 */
export const queryClaimsByPage = async ({limit = 10, page = 1}: { limit: number, page: number }) => {
	const resp = await post(`/user/XFUser/loanList`, {limit, page})
	resp.count = Number(resp.count)
	resp.page_count = Number(resp.page_count)
	return resp
}